import React from 'react';
import RaisedButton from 'material-ui/RaisedButton';

const DeleteButton = () => (
    /* eslint-disable react/jsx-boolean-value */
    <div>
        <RaisedButton label = 'Delete' secondary = { true } />
    </div>
);

export default DeleteButton;



